.question-card {
    /* border: 1.5px solid black; */
    border: 2px solid black;
    border-radius: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    padding: 2.4rem;
    width: 80%;

    overflow: scroll;
}

.infinitive-text,
.sentence-text,
.infinitive-text strong,
.sentence-text strong {
    font-size: 2.4rem;
}

.answer-input {
    padding: 0.6rem;
    width: 50%;
    font-size: 2.4rem;
    /* border: 1.5px solid black; */
    border: 2px solid black;
    background-color: #EFEFEF;
    transition: transform 0.15s ease-in-out, outline 0.15s ease-in-out;
}

.answer-input:active,
.answer-input:focus {
    transform: scale(1.0375);
    outline: 0;
}

.submit-button,
.toggle-button {
    padding: 0.6rem;
    font-size: 1.6rem;
    background-color: #E5E9F0;
    cursor: pointer;
    /* border: 1px solid black; */
    border: 2px solid black;
    border-radius: 5%;

    font-weight: 500;

    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.submit-button:disabled {
    background-color: #434C5E;
    color: #D8DEE9;
    cursor: default;
}

.submit-button:active {
    background-color: #D8DEE9;
}

.question-container {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 40%;
}

.input-and-button-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.2rem;
    width: 60%;
}

.text-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: flex-start;
    gap: 0.6rem;
}

.submit-and-emoji-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1.4rem;
}

.correctness-emoji {
    font-size: 2.8rem;
    background-color: transparent;
    text-shadow:
        1px 0px 0px #000,
        -1px 0px 0px #000,
        0px 1px 0px #000,
        0px -1px 0px #000,
        1px 1px 0px #000,
        -1px -1px 0px #000,
        1px -1px 0px #000,
        -1px 1px 0px #000;
}

@keyframes shake {

    0%,
    100% {
        transform: translateX(0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
        transform: translateX(-5px);
    }

    20%,
    40%,
    60%,
    80% {
        transform: translateX(5px);
    }
}

.shake {
    animation: shake 0.5s cubic-bezier(.36, .07, .19, .97) both;
}

.growAndShine {
    animation: growAndShine 0.5s ease-in-out both;
}

.infinitive-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1.2rem;
}

.speak-button {
    border: 0px;
    cursor: pointer;
    font-size: 2.4rem;
    padding: 0rem;
    margin: 0rem;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    background-color: transparent;
    text-shadow:
        1px 0px 0px #000,
        -1px 0px 0px #000,
        0px 1px 0px #000,
        0px -1px 0px #000,
        1px 1px 0px #000,
        -1px -1px 0px #000,
        1px -1px 0px #000,
        -1px 1px 0px #000;
}

.speak-button:active {
    transform: scale(1.3);
}

@keyframes growAndShine {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(2.25);
    }

    100% {
        transform: scale(1);
    }
}

.smooth-transition {
    transition: opacity 0.5s ease-in-out;
}

.low-opacity {
    opacity: 0.25;
}

.high-opacity {
    opacity: 1;
}

.sentence-text,
.infinitive-text {
    transition: opacity 0.5s linear;
}

/* Styles when data is loading. */
.low-opacity .sentence-text,
.low-opacity .infinitive-text {
    opacity: 0.25;
}