* {
    background-color: #D2E0FB;
}

.app-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2.4rem;
}

.container {
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.2rem;
    margin-bottom: 2.4rem;
}

.loading-txt {
    font-size: 2.4rem;
}

.verb-select {
    background-color: #EFEFEF;
    font-size: 2.4rem;
    border: 1.5px solid black;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

.fade-in {
    animation: fadeIn ease-in 0.5s;
    opacity: 1;
}

.fade-out {
    animation: fadeOut ease-in 0.5s;
    opacity: 0;
}

.conjugation-set-container {
    display: grid;
    grid-template-rows: repeat(auto, auto);
    grid-template-columns: repeat(2, 1fr);
    gap: 1.2rem;
    align-items: center;
    justify-items: center;
    width: 70%;
}

.left-column-item {
    justify-self: end;
}

.right-column-item {
    justify-self: start;
}

.conjugation-set {
    display: flex;
    flex-direction: row;
    gap: 1.2rem;
    justify-content: flex-start;
    align-items: center;
    align-self: flex-start;
}

.conjugation-set p {
    font-size: 1.6rem;
    font-weight: 900;
}

.toggle-button:active {
    background-color: #3B4252;
    color: white;
}

.hidden-conj {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.25s ease-in-out;
}

.visible-conj {
    opacity: 1;
    visibility: visible;
    transition: opacity 0.25s ease-in-out;
}

.smooth-transition {
    transition: opacity 0.5s ease-in-out;
}

.conjugation-row {
    display: flex;
    flex-direction: row;
    gap: 1.2rem;
}

.verb-button {
    padding: 0.2rem;
    font-size: 2rem;
    background-color: #ED7B7B;
    cursor: pointer;
    border: 2px solid black;
    border-radius: 5%;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

    font-weight: 500;
}

.verb-button:active {
    background-color: #F31559;
    color: white;
}

.delete-verb-button {
    font-size: 2.4rem;
    border: 0;
    font-weight: 700;
    text-shadow:
        1px 0px 0px #000,
        -1px 0px 0px #000,
        0px 1px 0px #000,
        0px -1px 0px #000,
        1px 1px 0px #000,
        -1px -1px 0px #000,
        1px -1px 0px #000,
        -1px 1px 0px #000;
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
}

.delete-verb-button:active {
    transform: scale(1.3);
}

@keyframes smackIn {
    0% {
        transform: translateY(-15%) scale(0.7);
        opacity: 0.3;
    }

    70% {
        transform: translateY(5%) scale(1.05);
    }

    100% {
        transform: translateY(0%) scale(1);
        opacity: 1;
    }
}

@keyframes shatterOut {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(1.2);
        opacity: 0;
    }
}

.conjugation-set {
    animation: smackIn 0.5s forwards;
}

.conjugation-set.remove {
    animation: shatterOut 0.5s forwards;
}

.select-disabled {
    opacity: 0.25;
    transition: opacity 0.5s ease-in-out;
}

.select-enabled {
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.keyboard {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
}